footer {
    width: 100%;
    padding-top: 100px;

    .inner {
        width: 1180px;
        margin: 0 auto;

        .upper {
            background-color: #fff;
            width: 1180px;
            height: 240px;
            border-bottom: 1.5px solid rgb(201, 201, 201);

            ul {
                &::after {content: ""; display: block; clear: both;}

                li {
                    float: left;
                    width: 25%;
                    
                    p {
                        font: bold 16px/2 "Poppins";
                        letter-spacing: 1px;
                        cursor: pointer;
                    }

                    &:nth-of-type(1) {
                        .footerPic {
                            width: 150px;
                            height: 150px;
                            background-image: url(../../public/img/umbrella.png);
                            background-repeat: no-repeat;
                            background-size: cover;
                        }
                    }

                    &:nth-of-type(2) {
                        a {
                            display: block;
                            font: bold 16px/2 "poppins";
                            letter-spacing: 1px;
                            color: #000;
                        }
                    }
                }
            }

        }

        .lower {
            background-color: #fff;
            padding-top: 90px;
            width: 1180px;
            height: 200px;
            ul {
                &::after {content: ""; display: block; clear: both;}

                .lowerLeft {
                    float: left;
                    >a {
                        font: bold 16px/1 "poppins";
                        letter-spacing: 1px;
                        color: #000;
                        margin-right: 20px;

                        i {
                            font-size: 20px;
                            margin-right: 10px;
                        }           
                    }
                    span {
                        font: bold 19px/1 "poppins";
                        color: #999;
                        letter-spacing: 1px;
                    }
                }

                .lowerRight {
                    float: right;
                    i {
                        font-size: 35px;
                        margin-left: 10px;
                        cursor: pointer;
                        color: #999;
                        transition: 0.3s;
                        
                        &:hover {
                            color: #333;
                            transition: 0.3s;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $tablet){
    footer {
        .inner {
            width: 100%;

            .upper {
                width: 100%;
                padding-right: 30px;
                padding-left: 30px;
            }

            .lower {
                width: 100%;
                padding-right: 30px;
                padding-left: 30px;
            }
        }
    }
}

@media screen and (max-width: $mobile){
    footer {
        padding-top: 0px;
        .inner {
            width: 100%;

            .upper {
                display: none;
            }

            .lower {
                background: crimson;
                width: 100%;
                height: 150px;
                padding-top: 30px;
                padding-right: 30px;
                padding-left: 30px;
                ul {
                    .lowerLeft {
                        display: block;
                        width: 100%;
                        text-align: center;
                        margin-bottom: 30px;
                        a {
                            display: none;
                        }
                        span {
                            color: #fff;
                        }
                    }
                    .lowerRight {
                        display: block;
                        width: 100%;
                        text-align: center;
                        i {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
}