.board {
    padding-top: 100px; 

    .inner {
        p {
            font: bold 18px/1 "poppins";
            margin-bottom: 60px;
        }

        .boardInner {
            &::after {content: ""; display: block; clear: both;}

            .boardLeft {
                float: left;
                width: 50%;
                padding-right: 100px;

                h1 {
                    font: bold 40px/1.8 "poppins";
                    letter-spacing: 0.5px;
                    color: #000;
                    margin-bottom: 100px;
                }

                #BoardSearchBox {
                    width: 515px;
                    border-bottom: 1px solid #000;
                    margin-bottom: 55px;
        
                    input[type=text] {
                        border: none;
                        width: 460px;
                        height: 40px;
                    }
                    input::placeholder {
                        font: bold 30px/1 "poppins";
                        color: rgb(200, 200, 200);
                        letter-spacing: 0.5px;
                    }
        
                    .btnSearch {
                        border: none;
                        font-size: 25px;
                        color: #999;
                        background-color: #fff;
                        transform: translateY(-10px);
                        cursor: pointer;
                        transition: 0.2s;
        
                        .arrowCircle {
                            width: 55px;
                            height: 55px;
                            border: 2px solid #999;
                            border-radius: 50%;
                            margin-bottom: 5px;
                            transition: 0.2s;
                            
                            i {
                                transform: translate(1px, 12px);
                            }
                        }
        
                        &:hover {
                            color: #fff;
                            transition: -0.2s;
        
                            .arrowCircle {
                                background-color: crimson;
                                border: 2px solid crimson;
                                transition: 0.2s;
                            }
                        }
                    }
                }

                ul {
                    li {
                        display: inline-block;
                        margin-right: 10px;
                        font: bold 17px/1 "poppins";
                        background-color: rgb(255, 217, 222);
                        color: #000;
                        padding: 7px 15px 7px 15px;
                        border-radius: 17px;
                        transition: 0.3s;
                        margin-bottom: 10px;
                        cursor: pointer;

                        &:hover {
                            background-color: crimson;
                            color: #fff;
                            transition: 0.3s;
                        }
                    }
                }
            }

            .boardRight {
                float: left;
                width: 50%;
                padding-bottom: 100px;
                
                // background-color: lightcoral;

                .boardDown {
                    transition: 0.2s;
                    &:hover {
                        background: #f8f8f8;
                        box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1);
                        transition: 0.2s;

                        .boardDownPost {
                            .boardDownNum {
                                transition: 0.2s;
                                h1 {
                                    color: #000;
                                    transition: 0.2s;
                                }
                            }
                            .boardDownTitle {
                                transition: 0.2s;
                                h3 {
                                    color: #000;
                                    transition: 0.2s;
                                }
                            }
                        }
                    }

                    .boardDownPost {
                        &::after {content: ""; display: block; clear: both;}
                        // border-bottom: 1px solid #000;
                        overflow: hidden;

                        .boardDownNum {
                            float: left;
                            width: 7%;
                            height: 70px;
                            padding-top: 10px;
                            // background-color: lightcyan;
                            h1 {
                                font: bold 20px/1 "poppins";
                                color: #999;
                                letter-spacing: 2px;
                                margin-left: 7px;
                            }
                        }

                        .boardDownTitle {
                            float: left;
                            width: 93%;
                            height: 70px;
                            padding-top: 10px;
                            // background-color: lightgreen;
                            overflow: hidden;
                            
                            h2 {
                                display: inline-block;
                                cursor: pointer;
                                font: bold 20px/1 "poppins";
                                letter-spacing: 0.5px;
                                color: crimson;
                                margin-bottom: 10px;
                                transition: 0.15s;
                                &:hover {
                                    box-shadow: 0 -2px 0 crimson inset;
                                    transition: 0.15s;
                                }
                            }
                            h3 {
                                font: bold 15px/1 "poppins";
                                letter-spacing: 1px;
                                color: #999;
                            }
                        }
                    }
                }
            }

            
        }

        .boardLower {
            // float: left;
            margin-bottom: 100px;
            table {
                width:100%; 
                // border-top: 2px solid #999;
                border-bottom:2px solid #999; 
                margin-bottom:50px;
                border-collapse:collapse;  
    
                tr {
                    &:hover {
                        td {
                            background:rgb(241, 241, 241); 
                        }
                    }
                }
    
                th {
                    padding:15px; 
                    background:#000; 
                    font: bold 17px/1 "poppins";
                    color:#555; 
                    color: #fff;
    
                    &:nth-of-type(1) {width:10%;}
                    &:nth-of-type(3) {width:15%;}
                    &:nth-of-type(4) {width:15%;}
                }
    
                td {
                    padding:20px; 
                    font: bold 17px/1 "poppins";
                    border-bottom:1px solid #ddd; 
                    text-align:center; 
                    color:#555; 
    
                    &:nth-of-type(1){color: crimson;}

                    &:nth-of-type(2) {
                        text-align:left;
                        cursor: pointer;
                    }
    
                    &:nth-of-type(3)  {
                        cursor: pointer;
                    }
                }
            }

            .pagination {
                margin-top: 50px;
                width: 100%;
                text-align: center;
                
                p {
                    width: 20px;
                    height: 20px;
                    display: inline-block;
                    cursor: pointer;

                    margin-right: 15px;
                    margin-left: 15px;
                    font: bold 15px/1 "poppins";
                    color: #999;

                    &:hover {
                        color: crimson;
                        // border-bottom: 2px solid crimson;
                    }

                    &:nth-of-type(2) {margin-left: 40px;}
                    &:nth-of-type(6) {margin-right: 20px;}
                }

                .leftCircle, .rightCircle {
                    font-size: 25px;
                    width: 40px;
                    height: 40px;
                    border: 2px solid #000;
                    color: #000;
                    border-radius: 50%;
                    line-height: 40px;
                    transition: 0.2s;

                    transform: translateY(3px);

                    &:hover {
                        color: #fff;
                        border-bottom: none;
                        border: 2px solid #555;
                        background-color: #555;
                        transition: 0.2s;
                    }
                }
            }
        }

        .boardCRUD {
            margin-bottom: 150px;
            h1 {
                font: bold 18px/1 "poppins";
                margin-bottom: 50px;
            }
            h2 {
                width: 80%;
                font: bold 40px/1.5 "poppins";
                margin-bottom: 50px;
            }

            .inputBox {
                width: 100%;
                padding: 40px 30px;
                border: 1px solid #555;
                position: relative;
                margin-bottom: 40px;

                input {
                    width: 60%;
                    padding: 10px 10px;
                    margin-bottom: 15px;
                    font: bold 15px/1 "poppins";
                }

                textarea {
                    resize: none;
                    width: 60%;
                    padding: 10px 10px;
                    font: bold 15px/1 "poppins";
                }

                .CRUDBtn {
                    position: absolute;
                    top: 40px;
                    right: 30px;

                    button {
                        cursor: pointer;
                        width: 80px;
                        height: 30px;
                        margin-left: 20px;
                        background: crimson;
                        border: none;
                        color: #fff;
                        font: bold 13px/1 "poppins";
                        transition: 0.2s;

                        &:hover {
                            transition: 0.2s;
                            background: #000;
                            color: #fff;
                        }
                    }
                }

                .CRUDcopy {
                    position: absolute;
                    bottom: 40px;
                    right: 30px;
                    color: crimson;
                    margin-bottom: 0px;
                }
            }

            .showBox {
                .showNotice {
                    width: 100%;
                    background: #000;
                    padding: 15px 30px;

                    p {    
                        font: bold 17px/1 "poppins";
                        letter-spacing: 1.5px;
                        color: #fff;
                        margin-bottom: 0px;
                    }
                }

                article {
                    width: 100%;
                    padding: 40px 30px;
                    border-bottom: 2px solid #ddd;
                    display: flex;
                    flex-wrap: wrap;
                    align-content: center;
                    justify-content: space-between;

                    h2 {
                        font: bold 20px/1 "poppins";
                        color: #000;
                        margin-bottom: 20px;
                    }
                    p {
                        font: bold 18px/1.2 "poppins";
                        color: #555;
                        margin-bottom: 0px;
                    }

                    .post {
                        width: 60%;
                        input {
                            width: 100%;
                            padding: 15px 10px;
                            border: none;
                            background: rgb(240, 240, 240);
                            margin-bottom: 15px;
                            font: bold 18px/1 "poppins";
                            color: #000;
                            letter-spacing: 1px;
                        }
    
                        textarea {
                            width: 100%;
                            padding: 15px 10px;
                            border: none;
                            background: rgb(240, 240, 240);
                            resize: none;
                            font: bold 15px/1 "poppins";
                            color: #000;
                            letter-spacing: 1px;
                        }
                    }

                    .btns {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        li {
                            cursor: pointer;
                            margin-left: 20px;
                            font-size: 15px;
                            font: bold 15px/1 "poppins";
                            color: #999;
                            transition: 0.25s;
                            &:nth-of-type(2) {color: crimson;}
                            i {
                                font-size: 25px;
                            }
                            &:hover {
                                transition: 0.25s;
                                color: crimson;
                            }
                        }
                    }
                }
            }
        }
    }
}

